import React from 'react';
import {Card,Form,Input,InputNumber} from 'antd';
import ColorPicker from 'rc-color-picker';
import 'rc-color-picker/assets/index.css';

const {Item}=Form;

const inlineFormItemLayout={
    labelCol:{
        sm:{span:6}
    },
    wrapperCol:{
        sm:{span:18}
    }
};

class NodeDetail extends React.Component{
    handleSubmit=()=>{
        /*
    * antdformthis.props,
    * formFormantd
    * getSelectedggeditorggeditorg6editor
    * updateItemggeditorggeditorg6editor
    * */
        const {form,getSelected,updateItem}=this.props

        //validateFieldAndScrollantdthis.props.form
        form.validateFieldsAndScroll((err,values)=>{
            if(err) return;

            const item=getSelected()[0]

            if(!item) return;

            values.size=`${values.width}*${values.height}`;
            values.color=values.color.color?values.color.color:values.color

            updateItem(item,{
                ...values
            });
        });
    };

    render(){
        const {form,getSelected}=this.props;
        //getFieldDecorator
        const {getFieldDecorator}=form;

        const item=getSelected()[0]

        if(!item) return null;

        const {label}=item.getModel()

        return(
            <Card type={'inner'} title={''} borderd={false}>
                <Form onSubmit={this.handleSubmit}>
                    <Item label={''} {...inlineFormItemLayout}>
                        {
                            getFieldDecorator('label',{
                                initialValue:label
                            })(<Input onBlur={this.handleSubmit}/>)
                        }
                    </Item>
                    <Item label={''} {...inlineFormItemLayout}>
                        {
                            getFieldDecorator('width',{
                                initialValue:this.props.getSelected()[0].getModel().size.split('*')[0]
                            })(<InputNumber onChange={this.handleSubmit} />)
                        }
                        {
                            getFieldDecorator('height',{
                                initialValue:this.props.getSelected()[0].getModel().size.split('*')[1]
                            })(<InputNumber onChange={this.handleSubmit} />)
                        }
                    </Item>
                    <Item label={''} {...inlineFormItemLayout}>
                        {
                            getFieldDecorator('color',{
                                initialValue:this.props.getSelected()[0].getModel().color
                            })(<ColorPicker
                                color={this.props.getSelected()[0].getModel().color}
                                animation={'slide-up'}
                                onChange={this.handleSubmit}
                                mode={'HSB'}/>)
                        }
                    </Item>
                </Form>
            </Card>
        )
    }
}


export default Form.create()(NodeDetail);